const MyButton = ({ text, type, onClick }) => {
  const btnType = ["positive", "negative"].includes(type) ? type : "default";
  //type      default  

  return (
    <button
      // join    2 .
      className={["MyButton", `MyButton_${btnType}`].join(" ")}
      onClick={onClick}
    >
      {text}
    </button>
  );
};

//   default .
MyButton.defaultProps = {
  type: "default",
};

export default MyButton;
